import React from 'react';
import styled from 'styled-components'

const FooterBlock = styled.div`

margin-top:30px;
background:#000;
text-align: center;
padding-bottom:30px;
.footerlogo{ 
    padding-top:30px;
    img{width:30%;}
}

.footerMenu {
    display:flex;
    justify-content:center;
    color: #fff;
    margin-top:20px;
   li{
       margin:0 20px;
  
   }
}
p{margin-top:20px; color: #fff;}

`

const Footer = () => {
    return (
        <div className="row">
             <FooterBlock>
                 <div className="footerlogo">
                 <img src="img/footer.png" alt="보그코리아"/>
                 </div>
                 <ul className="footerMenu">
                     <li><a href="#">정기구독</a> </li>
                     <li><a href="#">회사소개</a></li>
                     <li><a href="#">광고 및 제휴</a></li>
                     <li><a href="#">개인 처리 방침</a></li>
                </ul>
                <p>WWW.VOGUE.COM Ⓒ CONDÉNAST ASIA PACIFIC. INC. ALL RIGHTS RESERVED. VOGUE.COM KOREA IS OPERATED BY DOOSAN MAGAZINE.</p>
            </FooterBlock> 
        </div>
    );
};

export default Footer;