import React from 'react';
import "slick-carousel/slick/slick.css"; 
import Slider from "react-slick";
import styled from "styled-components"
import { AiFillCaretLeft } from "react-icons/ai";
import { AiFillCaretRight } from "react-icons/ai";

const Sliders = styled.div`
margin-top:200px;
.slick-arrow{
    width:60px;
    height:60px;
    position:absolute; top:50%; 
    transform:translateY(-50%);
    background:none; padding:10px;
    z-index:99999;
&.slick-prev{left:30px; color: #ff0000;};
&.slick-next{right:30px; color: #ff0000;};
};

.slick-dots{position:absolute; bottom:10px; left:50%; transform:translate(-50%);
li{ display:inline-block;
    &+li{padding-left:10px;} 
     button{width:20px; height:20px; border-radius:50%; background:#ddd;
        text-indent:-99999px; overflow:hidden; };
        &.slick-active {button{background:#ff0000;}}
};

}
.slide{
    background-size:cover;
    height:30vw !important;
    background-repeat:no-repeat;
    background-position:center;
    position: relative;
    .text {
            position:absolute; right:10%; text-align:center;
            width:40%; 
            top:50%; transform:translateY(-50%);
            color:#000; font-size:30px; font-family: 'Poppins', sans-serif;
            button{ 
                 padding:10px 20px; background:#ff0000; margin-top:10px;
                 border-radius:5px; border:none; color: #fff; font-size:20px}
        }
};
.slide1 {
    background-image:url(img/VogueSlide1.jpg)
};
.slide2 {
    background-image:url(img/VogueSlide2.jpg)
};
.slide3 {
    background-image:url(img/VogueSlide3.jpg)
};
`


const Slide = () => {
    const settings = {
        dots: true,
        infinite: true,
        speed:1000,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay:true,
        prevArrow:<AiFillCaretLeft /> ,
        nextArrow:<AiFillCaretRight />
      };
    


    return (
       <Sliders>
           <Slider {...settings}>
            <div className="slide slide1">
                
                <div className="text row">
                    <h2>레이디 디올’ 백을 <br/>재탄생시킨 아티스트</h2>
                    <button>자세히보기</button>
                </div>  
            </div>
            <div className="slide slide2">
                
                <div className="text row">
                    <h2>연말에 즐길수 있는<br/> 술들</h2>
                    <button>자세히보기</button>
                </div>  
            </div>
            <div className="slide slide3">
                
                <div className="text row">
                    <h2>힐링 스톤을 아시나요?</h2>
                    <button>자세히보기</button>
                </div>  
            </div>
            </Slider>
       </Sliders>    
    );
};

export default Slide;