import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import cn from 'classnames';
import './Header.scss';
import {TiSocialInstagram} from "react-icons/ti";
import {TiSocialFacebookCircular} from "react-icons/ti";
import {TiSocialTwitterCircular} from "react-icons/ti";
import {TiSocialYoutube} from "react-icons/ti";
import {TiZoomOutline} from "react-icons/ti";


const Header = () => {

    const [opener, setOpener] = useState(false);
    const openclose = () => {
        setOpener(!opener)
    };

    const [open, setOpen] = useState(false);
    const openClo = () => {
        setOpen(!open)
    };

    const [menuopen, setMenuOpen] = useState(false);
    const closeMenu = () => {
        setMenuOpen(!menuopen)
    };

    const useScroll = () => {

        const [scy, setScy] = useState({y:0})
        const onScroll = () => {
            setScy({
                y:window.scrollY
            })
        }
        useEffect(
            () => {
                window.addEventListener("scroll", onScroll)
                return () => window.removeEventListener("scroll",onScroll)
            },[]
        )
        return scy;
    }
   const {y} = useScroll();

    
    return (
        <div className="headerOuter" style={{backgroundColor:y ? "#F2F2F0":"#fff", height:y ? "150px":""}}>
            <div className="row">
                <div className="topMenu">
                    <ul className="sns">
                        <li><TiSocialInstagram/><span className="blind">인스타그램</span></li>
                        <li><TiSocialFacebookCircular/><span className="blind">페이스북</span></li>
                        <li><TiSocialTwitterCircular/><span className="blind">트위터</span></li>
                        <li><TiSocialYoutube/><span className="blind">유튜브</span></li>
                    </ul>
                    <ul className="depth1">
                        <li className="dp1" onClick={openclose}>
                            SIDE MENU
                            <ul className={cn("depth2",{opener})}>
                                <li>회사소개</li>
                                <li>광고및제휴</li>
                                <li>개인정보처리방침</li>
                            </ul>
                        </li>
                        <li className="dp1">
                        SUBSCRIBE
                        </li>
                    </ul>
                </div>
                <h1 className="logo"  style={{textAlign:y ? "left":"center"}} >
                <Link to="/"><img src="img/logo.png" alt="보그코리아" style={ {width:y ? "150px":"20%"}}/></Link>
                </h1>

                 <div className="mainMenu" style={{top:y ? "-100px":""}}>
                    <div className="menuClick" onClick={closeMenu}>Category</div>
                   <div className={cn("menuItem",{menuopen})}>
                        <Link to="/fashion" className="m1">Fashion</Link>
                        <Link to="/beauty" className="m1">Beauty</Link>
                        <Link to="/living" className="m1">Living</Link>
                        <Link to="/news" className="m1">News</Link>
                   </div>

                   <div className="relOuter" >
                     <TiZoomOutline className="select" onClick={openClo}/>
                     <div className={cn("retrieval",{open})}> 
                        <div className="retrievalBox">
                          <input type="text" placeholder="검색어 입력"/> 
                          <button type="submit">검색</button>
                        </div>
                     </div>
                   </div>

                </div> 

            </div>
        </div>
    );
};

export default Header;