import React from 'react';
import { Link } from 'react-router-dom';
import Slide from './Slide'
import './HomeSection.scss'


const HomeSection = () => {
    return (
        <div className="row">
         <Slide />
        <article className="article1">
            <Link to="/membership" className="itemBox">
                <img src="img/membership.jpg" alt="정기구독신청"/>
                <p>정기구독신청</p>
                <p>지금 신청하면 30% 할인</p>
            </Link>
            <div className="youTube" dangerouslySetInnerHTML={{__html:` <iframe width="100%" height="600px" src="https://www.youtube.com/embed/PB9PHR1suF0?mute=1&amp;autoplay=1&amp;rel=0" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`}} />
        </article> 

        <article className="article2">
        <Link to="/fashion" className="item001Box">
               <div className="fashionImg">
               <img src="img/fashion.jpg" alt="패션"/>
                   <h2>Fashion</h2>
                </div>
        </Link>
        <Link to="/beauty" className="item002Box">
               <div className="beautyImg">
               <img src="img/beauty.jpg" alt="뷰티"/>
                <h2>Beauty</h2>
                </div>
        </Link>
        </article>

        <article className="article3">
        <Link to="/living" className="item003Box">
               <div className="livingImg">
               <img src="img/living.jpg" alt="리빙"/>
                   <h2>Living</h2>
                </div>
        </Link>
        <Link to="/news" className="item004Box">
               <div className="newsImg">
               <img src="img/news.jpg" alt="뉴스"/>
                   <h2>News</h2>
                </div>
        </Link>
        </article>
    </div>
    );
};

export default HomeSection;